//-----------------------------------------------------------------------//
// STYLE GUIDE
//-----------------------------------------------------------------------//

@import "manifest";

// Custom units from the "custom" folder will be inserted here dynamically when the site is launched.
// DO NOT EDIT BETWEEN THE FOLLOWING COMMENTS.
// $CARBIDE_PARTIALS:BEGIN
@import "custom/_buttons.scss";
@import "custom/_fonts.scss";
@import "custom/_forms.scss";
@import "custom/_grids.scss";
@import "custom/_layout-footer.scss";
@import "custom/_layout-header.scss";
@import "custom/_lists-and-tables.scss";
@import "custom/_media.scss";
@import "custom/_overrides_pikaday.scss";
@import "custom/_typography.scss";
@import "custom/carbide/basic_footer_navigation_unit.scss";
@import "custom/carbide/basic_primary_navigation_unit.scss";
@import "custom/carbide/basic_theme.scss";
@import "custom/carbide/elastic_container_unit.scss";
@import "custom/carbide/fluidbox.scss";
@import "custom/carbide/form_subunit.scss";
@import "custom/carbide/gallery_view.scss";
@import "custom/carbide/multi_column_content_subunit.scss";
@import "custom/carbide/portfolio_view.scss";
@import "custom/carbide/rich_content_subunit.scss";
@import "custom/carbide/simple_hero_unit.scss";
// $CARBIDE_PARTIALS:END

:root {

    --background-color: var(--sf-color-white);
    --foreground-color: var(--sf-color-white-contrast);
    --anchor-color: var(--sf-color-primary);
    --button-color: var(--sf-color-secondary);
    --h1-color: var(--sf-color-white-contrast);
    --h2-color: var(--sf-color-white-contrast);
    --h3-color: var(--sf-color-white-contrast);
    --h4-color: var(--sf-color-white-contrast);
    --h5-color: var(--sf-color-white-contrast);
    --h6-color: var(--sf-color-white-contrast);
}

html, body {

    background-color: var(--background-color);
    color: var(--foreground-color);

    a, a:hover, a:visited, a:active, .a {

        color: var(--anchor-color);
    }

    button, input[type="button"], input[type="submit"], input[type="cancel"], a[role="button"] {

        background-color: var(--button-color);
        border-color: var(--button-color);
    }

    h1, .h1 {

        color: var(--h1-color);
    }

    h2, .h2 {

        color: var(--h2-color);
    }

    h3, .h3 {

        color: var(--h3-color);
    }

    h4, .h4 {

        color: var(--h4-color);
    }

    h5, .h5 {

        color: var(--h5-color);
    }

    h6, .h6 {

        color: var(--h6-color);
    }
}

body {
    
    & > main {

        > section[separator] {

            @include set-bottom-unit-padding();
        }

        // Primary wrapped containers with an elastic max-width
        > [sf-unit] {

            //background-color: white;
            overflow: hidden;

            & + [sf-unit] {

                border-top: solid 1px var(--sf-color-line);
            }
        }
    }
}

